import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { dropdownStyles } from './dropdownStyle';
import { AnimationVariant, DropdownEventClose, DropdownEventToggle, DropdownProperties } from './dropdownProperties';
import { DropdownPlacement } from './dropdownPlacement';
import { version } from './metadata';
import { Button } from '@umetrics/sartorius-ui-button';

@defineElement({ tagName: 'dropdown', styles: dropdownStyles, version })
export class Dropdown extends BaseElement implements DropdownProperties {
    // Static members
    private static Scrollbar;
    // Private members

    // Protected members

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    open = false;
    @property({ type: Boolean, converter: booleanConverter })
    disabled = false;
    @property({ type: Boolean, attribute: 'click-outside-close', converter: booleanConverter })
    clickOutsideClose = true;
    @property({ type: DropdownPlacement, attribute: 'force-placement' }) forcePlacement?: DropdownPlacement;

    @property({ type: Boolean, converter: booleanConverter })
    controlled = false;

    @property({ type: Object })
    public object: HTMLElement | null = null;
    @property({ type: Object })
    public parentObject: HTMLElement | null = null;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'fixed-width' })
    fixedWidth = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'no-overflow' })
    noOverflow = false;

    @property({ type: String, reflect: true, attribute: 'object-id' })
    public objectId: string | null = null;
    @property({ type: AnimationVariant, attribute: 'animation-variant' })
    animationVariant = AnimationVariant.Default;

    @state()
    private placement = DropdownPlacement.BelowLeftAligned;
    @query('.trigger')
    private trigger?: HTMLElement;
    @query('.container')
    private container?: HTMLElement;
    private _firstRenderComplete = false;
    private _intersectionObserver: IntersectionObserver | null = null;

    connectedCallback() {
        if (!Dropdown.Scrollbar) {
            const imports: any[] = [];
            imports.push({ module: () => import('@umetrics/sartorius-ui-scrollbar'), elem: ['Scrollbar'] });
            Dropdown.dynSubRegister(imports, () => {
                super.connectedCallback();
            });
        } else {
            super.connectedCallback();
        }

        this._toggle = this._toggle.bind(this);
        this._handleResize = this._handleResize.bind(this);
        this._handleScroll = this._handleScroll.bind(this);
        this._handleIntersection = this._handleIntersection.bind(this);
        this._handleWindowClick = this._handleWindowClick.bind(this);
    }

    disconnectedCallback() {
        window.removeEventListener('scroll', this._handleScroll, true);
        window.removeEventListener('resize', this._handleResize);
        window.removeEventListener('click', this._handleWindowClick, true);
        if (this._intersectionObserver !== null) {
            this._intersectionObserver.disconnect();
        }
        if (this.object) {
            this.object.removeEventListener('click', this._toggle);
        }
        super.disconnectedCallback();
    }

    protected async firstUpdated(c) {
        if (this.objectId && this.object === null && (this.parentElement || this.parentObject)) {
            const parent = this.parentObject ? this.parentObject : this.parentElement!;
            this.object = parent.querySelector(`#${this.objectId}`);
        }
        if (this.object !== null) {
            this.object.addEventListener('click', this._toggle);
        }
        this.placement = this._calculatePlacement();
        this._initialStyle();
        this._firstRenderComplete = true;
        if (this.open) {
            window.requestAnimationFrame(() => {
                this.placement = this._calculatePlacement();
                this._updateOpen();
            });
        }
        super.firstUpdated(c);
    }

    protected update(changedProperties) {
        if (changedProperties.has('open') && this._firstRenderComplete) {
            this._shouldUpdateOpen();
        }
        if (changedProperties.has('navigated')) {
            this.placement = this._calculatePlacement();
            this._checkWindowBoundaries();
        }
        if (changedProperties.has('forcePlacement')) {
            this.placement = this.forcePlacement ?? DropdownPlacement.BelowLeftAligned;
        }
        if (changedProperties.has('clickOutsideClose') && this.open) {
            this._updateClickOutsideClose();
        }

        const prevObjectId = changedProperties.get('objectId');
        if (prevObjectId !== undefined) {
            if (this.objectId !== null && this.parentElement) {
                this.object = this.parentElement.querySelector(`#${this.objectId}`);
            } else {
                this.object = null;
            }
        }

        const prevObject = changedProperties.get('object');
        if (prevObject !== undefined) {
            this._updateObject(prevObject);
        }
        super.update(changedProperties);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            open: this.open,
            fixedWidth: this.fixedWidth,
            noOverflow: this.noOverflow,
        });
        const scrollableHtml = this.noOverflow
            ? html`
                  <div class="${baseClassName}-items container ${this.placement}">
                      <slot></slot>
                  </div>
              `
            : html`
                <${Dropdown.Scrollbar.tag} class="${baseClassName}-items container ${this.placement}">
                    <slot></slot>
                </${Dropdown.Scrollbar.tag}>
            `;
        return html`
            <div
                @click=${(e) => {
                    if (this.object == null) e.stopPropagation();
                }}
                class="${rootClasses}"
            >
                <div class="${baseClassName}-button trigger"></div>
                ${scrollableHtml}
            </div>
        `;
    }

    private _initialStyle() {
        this.style.pointerEvents = 'none';
        this.style.position = 'fixed';
        this.style.zIndex = '10';
    }

    private _checkObject() {
        const objectRect = this.object!.getBoundingClientRect();
        this.trigger!.style.width = objectRect.width + 'px';
        this.trigger!.style.height = objectRect.height + 'px';
        this.trigger!.style.left = objectRect.left + 'px';
        this.trigger!.style.top = objectRect.top + 'px';
        this.style.top = this.object!.offsetTop + 'px';
        this.style.left = this.object!.offsetLeft + 'px';
        if (this.trigger && this.trigger.offsetParent && this.getBoundingClientRect().top !== objectRect.top) {
            this.style.top = objectRect.top + 'px';
            this.style.left = objectRect.left + 'px';
        } else {
            this.style.top = this.object!.offsetTop + 'px';
            this.style.left = this.object!.offsetLeft + 'px';
        }
    }

    private _calculatePlacement(): DropdownPlacement {
        if (!this.trigger || !this.container) return DropdownPlacement.BelowLeftAligned;
        if (this.object !== null) {
            this._checkObject();
        }

        if (this.forcePlacement) {
            return this.forcePlacement;
        }
        const { clientWidth: containerWidth, clientHeight: containerHeight } = this.container;

        return this._decidePlacement(containerWidth, containerHeight);
    }

    //These 2 functions below only exist because of the annoying code smell brain load...

    private _decidePlacement(containerWidth, containerHeight) {
        const triggerRect = this.trigger!.getBoundingClientRect();

        const { width: triggerWidth, height: triggerHeight } = triggerRect;
        const { spaceAbove, spaceBelow, spaceToLeft, spaceToRight } = this._calculateSpace(triggerRect);
        if (spaceBelow > containerHeight && spaceToRight + triggerWidth > containerWidth) {
            return DropdownPlacement.BelowLeftAligned;
        } else if (spaceBelow > containerHeight && spaceToLeft > containerWidth) {
            return DropdownPlacement.BelowRightAligned;
        } else if (spaceToRight > containerWidth && spaceBelow + triggerHeight > containerHeight) {
            return DropdownPlacement.RightSideTopAligned;
        } else if (spaceToLeft > containerWidth && spaceBelow + triggerHeight > containerHeight) {
            return DropdownPlacement.LeftSideTopAligned;
        } else if (spaceToRight > containerWidth && spaceBelow > -1 && spaceAbove + triggerHeight > containerHeight) {
            return DropdownPlacement.RightSideBottomAligned;
        } else if (spaceToLeft > containerWidth && spaceBelow > -1 && spaceAbove + triggerHeight > containerHeight) {
            return DropdownPlacement.LeftSideBottomAligned;
        } else if (spaceAbove > containerHeight) {
            return this._checkAbovePlacement(spaceToRight, spaceToLeft, triggerWidth, containerWidth);
        }

        return this.placement; // Last good
    }

    private _checkAbovePlacement(spaceToRight, spaceToLeft, triggerWidth, containerWidth) {
        if (spaceToLeft + triggerWidth > containerWidth) {
            return DropdownPlacement.AboveRightAligned;
        } else if (spaceToRight + triggerWidth > containerWidth) {
            return DropdownPlacement.AboveLeftAligned;
        } else return this.placement;
    }

    private _calculateSpace(triggerRect: DOMRect) {
        const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        const windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;

        const spaceAbove = triggerRect.top;
        const spaceBelow = windowHeight - triggerRect.bottom;
        const spaceToLeft = triggerRect.left;
        const spaceToRight = windowWidth - triggerRect.right;

        return { spaceAbove, spaceBelow, spaceToLeft, spaceToRight };
    }

    private _updateObject(prevObject) {
        if (prevObject !== null) {
            prevObject.removeEventListener('click', this._toggle);
        }
        if (this.object !== null) {
            this.object.addEventListener('click', this._toggle);
            if (this.open) {
                this.placement = this._calculatePlacement();
                this.open = false;
                this.requestUpdate('open', true);
                setTimeout(() => {
                    this.placement = this._calculatePlacement();
                    this.open = true;
                    this.requestUpdate('open', false);
                }, 150);
            }
        }
    }

    private _updateClickOutsideClose() {
        if (this.clickOutsideClose) {
            window.addEventListener('click', this._handleWindowClick, true);
        } else {
            window.removeEventListener('click', this._handleWindowClick, true);
        }
    }

    private _shouldUpdateOpen() {
        if (this.open) {
            this.placement = this._calculatePlacement();
            this._checkWindowBoundaries();
            this._updateOpen();
        } else {
            if (this.object instanceof Button) {
                const button = this.object;
                button.active = false;
            }
            this._updateClose();
        }
    }

    private _updateOpen() {
        if (this.animationVariant !== AnimationVariant.None) {
            this._startAnimation(this.animationVariant === AnimationVariant.Default ? 'show' : 'expand');
        }
        window.addEventListener('scroll', this._handleScroll, true);
        window.addEventListener('resize', this._handleResize);

        if (this.clickOutsideClose) {
            window.addEventListener('click', this._handleWindowClick, true);
        }
        this._installIntersectionObserver();
    }

    private _updateClose() {
        if (this.animationVariant !== AnimationVariant.None) {
            this._startAnimation(this.animationVariant === AnimationVariant.Default ? 'hide' : 'retract');
        }
        window.removeEventListener('scroll', this._handleScroll, true);
        window.removeEventListener('resize', this._handleResize);

        if (this.clickOutsideClose) {
            window.removeEventListener('click', this._handleWindowClick, true);
        }
        this._uninstallIntersectionObserver();
    }

    private _checkWindowBoundaries() {
        const windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
        const rect = this.trigger ? this.trigger.getBoundingClientRect() : { x: 0, y: 0, height: 0, top: 0 };
        let placedBelow = true;
        let checkedPosition = rect.y;
        let calculatedPlacement = windowHeight - checkedPosition;
        switch (this.placement) {
            case DropdownPlacement.BelowRightAligned:
            case DropdownPlacement.LeftSideTopAligned:
            case DropdownPlacement.RightSideTopAligned:
            case DropdownPlacement.BelowLeftAligned: {
                checkedPosition = rect.y;
                calculatedPlacement = windowHeight - checkedPosition;

                break;
            }
            case DropdownPlacement.AboveLeftAligned:
            case DropdownPlacement.RightSideBottomAligned:
            case DropdownPlacement.LeftSideBottomAligned:
            case DropdownPlacement.AboveRightAligned: {
                placedBelow = false;
                checkedPosition = rect.top;
                calculatedPlacement = checkedPosition;
                break;
            }
        }

        if (calculatedPlacement < this.container!.scrollHeight) {
            if (placedBelow) {
                this.container!.style.height = calculatedPlacement - rect.height - 28 + 'px';
            } else {
                this.container!.style.height = calculatedPlacement - 18 + 'px';
            }
            this.container!.style.minHeight = 40 + 'px';
            this.container!.style.overflowX = 'hidden';
        } else {
            this.container!.style.removeProperty('height');
        }
    }

    private _close = async () => {
        this.placement = this._calculatePlacement();
        if (this.open) {
            if (!this.controlled) {
                this.open = false;
            }
            this._toggleParentButton();
            await this.updateComplete;

            this.dispatchEvent(
                new CustomEvent('close', {
                    detail: { newState: false, prevState: this.open } as DropdownEventClose,
                    bubbles: true,
                    composed: true,
                })
            );
        }
    };

    private _handleIntersection(entries) {
        entries.forEach((entry) => {
            if (!entry.isIntersecting) {
                this._close();
            }
        });
    }

    private _handleResize() {
        this._toggle();
    }

    private _handleScroll() {
        if (this.open) {
            this.placement = this._calculatePlacement();
            setTimeout(() => {
                this._checkWindowBoundaries();
            }, 500);
        }
    }

    private _handleWindowClick(e) {
        if (
            e.target !== this.object &&
            e.composedPath().indexOf(this.object) === -1 &&
            !e.composedPath().includes(this)
        ) {
            this._close();
        }
    }

    private _startAnimation(anim) {
        if (this.container) {
            this.container.style.animationName = anim;
            this.container.style.overflow = 'hidden';

            const handler = setTimeout(() => {
                this.container!.style.animationName = '';
            }, 300);
            // eslint-disable-next-line prefer-const
            let noAnimationHandler: ReturnType<typeof setTimeout>;
            const animationHandler = () => {
                this.container!.style.animationName = '';
                this.container!.style.removeProperty('overflow');
                clearTimeout(handler);
                clearTimeout(noAnimationHandler);
            };
            noAnimationHandler = setTimeout(() => {
                this.container!.style.removeProperty('overflow');
                this.container!.removeEventListener('animationend', animationHandler);
            }, 350);
            this.container.addEventListener('animationend', animationHandler, { once: true });
        }
    }

    private async _toggle(e?: MouseEvent) {
        this.placement = this._calculatePlacement();

        if (this.fixedWidth) {
            this.container!.style.setProperty('overflowX', 'hidden');
        } else {
            this.container!.style.removeProperty('overflowX');
        }

        this._checkWindowBoundaries();
        if (!this.controlled) {
            this.open = !this.open;
        }
        this._toggleParentButton();
        await this.updateComplete;

        this.dispatchEvent(
            new CustomEvent('toggle', {
                detail: { newState: !this.open, prevState: this.open, triggerEvent: e } as DropdownEventToggle,
                bubbles: true,
                composed: true,
            })
        );
    }

    private _toggleParentButton() {
        if (this.object instanceof Button) {
            const button = this.object;
            if (this.open) button.active = true;
            else button.active = false;
        }
    }

    private _uninstallIntersectionObserver() {
        if (this._intersectionObserver !== null && this.object) {
            this._intersectionObserver.unobserve(this.object);
        }
    }

    private _installIntersectionObserver() {
        if (this.object) {
            if (this._intersectionObserver === null) {
                this._intersectionObserver = new window.IntersectionObserver(this._handleIntersection, {
                    root: null,
                    rootMargin: '0px',
                    threshold: 0.0 /* 1.0 doesnt work on safari */,
                });
            }
            this._intersectionObserver.observe(this.object);
        }
    }
}
