import { DropdownPlacement } from './dropdownPlacement';

export interface Size {
    width: number;
    height: number;
}

export enum AnimationVariant {
    None = 'none',
    Default = 'default',
    Select = 'select',
}

export interface DropdownEventToggle {
    newState: boolean;
    prevState: boolean;
    triggerEvent?: MouseEvent;
}

export interface DropdownEventClose {
    newState: boolean;
    prevState: boolean;
}

export interface DropdownProperties {
    open?: boolean;
    disabled?: boolean;
    forcePlacement?: DropdownPlacement;
    clickOutsideClose?: boolean;
    controlled?: boolean;
    fixedWidth?: boolean;
    noOverflow?: boolean;

    parentObject?: HTMLElement | null;
    objectId?: string | null; // Deprecated, from now please use object property.
    object?: HTMLElement | null;

    /* Emitted events, for react users */
    onToggle?: (e: CustomEvent<DropdownEventToggle>) => void;
    onClose?: (e: CustomEvent<DropdownEventClose>) => void;
}
