import { css, CSSResultArray, cssSelector, GenerateStyles, unsafeCSS } from '@umetrics/sartorius-ui-base';
import { DropdownPlacement } from './dropdownPlacement';

// see https://www.sartorius.com/brand-platform/applications/digital

export const dropdownStyles: GenerateStyles = (tagName) => {
    const { child, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: inline-block;
            }

            :host([disabled='true']) * {
                pointer-events: none;
            }

            ${child('items')} {
                position: absolute;
                pointer-events: none;
                box-shadow: 0px 3px 6px #00000026;
                background: var(--dropdown-background, var(--color-primary-2, #fff));
                padding: var(--dropdown-padding, 16px 0);
                margin: 0;
                opacity: 0;
                z-index: 1;
                animation-duration: 0.25s;
                visibility: hidden;
                display: block;
                width: initial;
                height: initial;
            }

            ${child('container')} {
                max-height: 100vh;
            }

            ${is('open')} ${child('items')} {
                pointer-events: auto;
                opacity: 1;
                animation-duration: 0.25s;
                visibility: visible;
            }

            ${is('fixedWidth')} ${child('items')} {
                width: 100%;
            }

            ${is('noOverflow')} ${child('items')} {
                overflow: hidden;
            }

            @keyframes show {
                0% {
                    transform: scale(1);
                    opacity: 0;
                    visibility: visible;
                }
                1% {
                    transform: scale(0);
                    opacity: 0;
                }
                99% {
                    transform: scale(1);
                    opacity: 1;
                }
            }
            @keyframes hide {
                0% {
                    transform: scale(1);
                    opacity: 1;
                    visibility: visible;
                }
                99% {
                    transform: scale(0);
                    opacity: 0;
                    visibility: visible;
                }
                100% {
                    transform: scale(1);
                    opacity: 0;
                    visibility: hidden;
                }
            }
            @keyframes expand {
                0% {
                    max-height: 100vh;
                    opacity: 0;
                    visibility: visible;
                }
                1% {
                    max-height: 0px;
                    opacity: 0;
                }
                99% {
                    max-height: 100vh;
                    opacity: 1;
                }
            }
            @keyframes retract {
                0% {
                    max-height: 100vh;
                    opacity: 1;
                    visibility: visible;
                }
                99% {
                    max-height: 0px;
                    opacity: 0;
                    visibility: visible;
                }
                100% {
                    max-height: 100vh;
                    opacity: 0;
                    visibility: hidden;
                }
            }

            .${unsafeCSS(DropdownPlacement.BelowRightAligned)} {
                top: 100%;
                right: 0;
                transform-origin: top right;
            }

            .${unsafeCSS(DropdownPlacement.BelowLeftAligned)} {
                top: 100%;
                left: 0;
                transform-origin: top left;
            }

            .${unsafeCSS(DropdownPlacement.RightSideTopAligned)} {
                top: 0;
                left: 100%;
                transform-origin: top left;
            }

            .${unsafeCSS(DropdownPlacement.RightSideBottomAligned)} {
                bottom: 0;
                left: 100%;
                transform-origin: bottom left;
            }

            .${unsafeCSS(DropdownPlacement.LeftSideTopAligned)} {
                top: 0;
                right: 100%;
                transform-origin: top right;
            }

            .${unsafeCSS(DropdownPlacement.LeftSideBottomAligned)} {
                bottom: 0;
                right: 100%;
                transform-origin: bottom right;
            }

            .${unsafeCSS(DropdownPlacement.AboveRightAligned)} {
                bottom: 100%;
                right: 0;
                transform-origin: bottom right;
            }

            .${unsafeCSS(DropdownPlacement.AboveLeftAligned)} {
                bottom: 100%;
                left: 0;
                transform-origin: bottom left;
            }
        `,
    ] as CSSResultArray;
};
